import Vue from 'vue'
import isString from 'lodash.isstring'

import MessageBox from './message-box.vue'
import Notifier from './notifier.vue'

import { isZh } from '../../utils/language'

const alertTitle = isZh ? 'æç¤º' : 'Alert'
const errorTitle = isZh ? 'éè¯¯' : 'Error'
const confirmTitle = isZh ? 'ç¡®è®¤æç¤º' : 'Confirm'
const warnTitle = isZh ? 'ç¡®è®¤è­¦å' : 'Warning'

const dialogButtons = {
  ok: {
    id: 'ok',
    caption: isZh ? 'ç¡®å®' : 'OK',
    action: 'close'
  },
  cancel: {
    id: 'cancel',
    caption: isZh ? 'åæ¶' : 'CANCEL',
    action: 'close'
  },
  yes: {
    id: 'yes',
    caption: isZh ? 'æ¯' : 'YES',
    action: 'close'
  },
  no: {
    id: 'no',
    caption: isZh ? 'å¦' : 'NO',
    action: 'close'
  }
}

export function showMessage (options) {
  const { title, message, buttons, primaryButton, danger, callback } = options

  return new Promise(resolve => {
    const dialog = new Vue({
      extends: MessageBox,
      title,
      message,
      danger,
      buttons: buttons.map(
        btn => isString(btn) ? (dialogButtons[btn] || btn) : btn
      ),
      primaryButton: primaryButton === undefined
        ? buttons[0]
        : primaryButton
    })
    dialog.$once('hide', btn => {
      if (callback) callback(btn)
      resolve(btn)
    })
    dialog.show()
  })
}

export function alert (message, callback) {
  return showMessage({
    title: alertTitle,
    buttons: ['ok'],
    message,
    callback
  })
}

export function error (message, callback) {
  return showMessage({
    title: errorTitle,
    buttons: ['ok'],
    danger: true,
    message,
    callback
  })
}

export function confirm (message, callback) {
  return showMessage({
    title: confirmTitle,
    buttons: ['ok', 'cancel'],
    message,
    callback
  })
}

export function warn (message, callback) {
  return showMessage({
    title: warnTitle,
    buttons: ['ok', 'cancel'],
    danger: true,
    message,
    callback
  })
}

let notifier

export function notify (notifyType, message, timeout, onClickHandler) {
  if (!notifier) {
    notifier = new Vue(Notifier).$mount()
    document.body.appendChild(notifier.$el)
  }
  notifier.notify(notifyType, message, timeout, onClickHandler)
}
